<template>
    <div class="container">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户头像" prop="avatarUrl">
                <!-- element 上传图片按钮 -->
                <el-upload class="upload-demo" action="" drag accept="image/jpeg,image/gif,image/png"
                           :auto-upload="false" :show-file-list="false" :on-change='changeUpload'>
                    <img :src="ruleForm.avatarUrl" class="pic">
                   <!-- <el-avatar :src="ruleForm.avatarUrl" class="pic" :size="120"></el-avatar>-->
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">点击上传</div>
                </el-upload>
            </el-form-item>
            <el-form-item label="用户账号" prop="account">
                <el-input placeholder="请输入用户账号" v-model="ruleForm.account"></el-input>
            </el-form-item>
            <el-form-item label="用户名" prop="name">
                <el-input placeholder="请输入用户名" v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="用户昵称" prop="nick">
                <el-input placeholder="请输入昵称" v-model="ruleForm.nick"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="gender">
                <el-radio-group v-model="ruleForm.gender">
                    <el-radio label="0">男</el-radio>
                    <el-radio label="1">女</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="手机号码" prop="phone">
                <el-input placeholder="请输入手机号码" maxlength="11" v-model="ruleForm.phone"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-news">保存</el-button>
                <el-button @click="resetForm('ruleForm')" icon="el-icon-refresh">重置</el-button>
            </el-form-item>
        </el-form>

        <!-- vueCropper 剪裁图片实现-->
        <el-dialog title="剪裁图片" :visible.sync="dialogVisible" append-to-body>
            <div class="cropper-content">
                <div class="cropper" style="text-align:center">
                    <vueCropper
                            ref="cropper"
                            :img="option.img"
                            :outputSize="option.size"
                            :outputType="option.outputType"
                            :info="true"
                            :full="option.full"
                            :canMove="option.canMove"
                            :canMoveBox="option.canMoveBox"
                            :original="option.original"
                            :autoCrop="option.autoCrop"
                            :fixed="option.fixed"
                            :fixedNumber="option.fixedNumber"
                            :centerBox="option.centerBox"
                            :infoTrue="option.infoTrue"
                            :fixedBox="option.fixedBox"
                            @realTime="realTime"
                    ></vueCropper>
                </div>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false" icon="el-icon-refresh-left">取消</el-button>
                <el-button type="primary" @click="finish" :loading="loading" icon="el-icon-upload2">上传</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<style>
    .container {
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        justify-content: center;
        height: 700px;
    }

    .demo-ruleForm {
        width: 50%;
    }

    .el-upload-dragger {
        max-width: 120px;
        max-height: 120px;
    }

    .pic {
        max-width: 120px;
        max-height: 120px;
    }

    .cropper-content {
        width: auto;
        height: 200px;
    }

    .cropper {
        width: auto;
        height: 200px;
    }

</style>
<script>
   /* import VueUeditorWrap from 'vue-ueditor-wrap'*/

    export default {
       /* components: {VueUeditorWrap},*/
        data() {
            return {
                ruleForm: {
                    userId: '',
                    account: '',
                    name: '',
                    nick: '',
                    gender: '0',
                    phone: '',
                    avatarUrl: '',
                },
                previews: {},
                dialogVisible: false,
                loading: false,
                option: {
                    img: '', // 裁剪图片的地址
                    info: true, // 裁剪框的大小信息
                    outputSize: 1, // 裁剪生成图片的质量
                    outputType: 'png', // 裁剪生成图片的格式
                    canScale: false, // 图片是否允许滚轮缩放
                    autoCrop: true, // 是否默认生成截图框
                    // autoCropWidth: 200, // 默认生成截图框宽度
                    // autoCropHeight: 200, // 默认生成截图框高度
                    fixedBox: false, // 固定截图框大小 不允许改变
                    fixed: true, // 是否开启截图框宽高固定比例
                    fixedNumber: [1, 1], // 截图框的宽高比例
                    full: true, // 是否输出原图比例的截图
                    canMoveBox: true, // 截图框能否拖动
                    original: false, // 上传图片按照原始比例渲染
                    centerBox: true, // 截图框是否被限制在图片里面
                    infoTrue: true // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
                },
                rules: {
                    account: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                        {min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur'}
                    ],
                    name: [
                        {required: true, message: '请输入用户名', trigger: 'blur'},
                        {min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur'}
                    ],
                    nick: [
                        {min: 0, max: 10, message: '长度在 0 到 10 个字符', trigger: 'blur'}
                    ],
                    phone: [
                        {required: true, message: '请输入手机号码', trigger: 'blur'},
                        {min: 11, max: 11, message: '长度在11个字符', trigger: 'blur'}
                    ]
                }
            }
        },
        mounted() {
            this.axios.get('/api/user/user/info').then((response) => {
                    if (response.data.data != null) {
                        this.ruleForm.userId = response.data.data.userId;
                        this.ruleForm.account = response.data.data.account;
                        this.ruleForm.name = response.data.data.name;
                        this.ruleForm.nick = response.data.data.nick;
                        this.ruleForm.gender = response.data.data.gender;
                        this.ruleForm.phone = response.data.data.phone;
                        this.ruleForm.avatarUrl = response.data.data.avatarUrl;
                    }
                }
            ).catch((response) => {
                console.log(response);
            })
        },
        methods: {
            // 上传按钮
            changeUpload(file) {
                //上传图片
                const isIMAGE = (file.raw.type === 'image/jpeg' || file.raw.type === 'image/png' || file.raw.type === 'image/gif');
                if (!isIMAGE) {
                    this.$message({
                        type: 'info',
                        message: '上传文件只能是图片格式!'
                    });
                    return false;
                }
                var reader = new FileReader();
                reader.readAsDataURL(file.raw);
                let that = this;
                reader.onload = function () {
                    that.dialogVisible = true;
                    that.option.img = this.result;
                }
            }, realTime(data) {
                this.previews = data;
            },
            // 点击裁剪，这一步是可以拿到处理后的地址
            finish() {
                this.$refs.cropper.getCropBlob((data) => {
                    this.loading = true;
                    this.dialogVisible = false;

                    let formData = new FormData()
                    formData.append('file', data);

                    let config = {
                        headers: {'Content-Type': 'multipart/form-data'}
                    }
                    // 添加请求头
                    this.axios.post('/api/upload/file', formData, config).then(response => {
                        if (response.data.state == 0) {
                            this.ruleForm.avatarUrl = response.data.data;
                            this.loading = false;
                        } else {
                            this.loading = false;
                            this.$message({
                                type: 'info',
                                message: '上传失败'
                            });
                        }
                    })
                })
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.axios.post('/api/user/info/save',
                            {
                                ...this.ruleForm

                            }).then((response) => {
                            if (response.data.state == '0') {
                                this.$message({
                                    type: 'info',
                                    message: "保存成功"
                                });
                            } else {
                                this.$message({
                                    type: 'info',
                                    message: response.data.message
                                });
                            }
                        }).catch((response) => {
                            console.log(response);
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
            ,
        }
        ,
    }
</script>
